import * as React from "react";
import { Button, Tag } from 'antd-mobile'

//redux 基础组件加载
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as Actions from '../actions';
import { getCourseSeries } from '../api/index';


import '../assets/less/allsearch.less'


const mapStateToProps = (state: any) => {
    return {
        store: state
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        ...bindActionCreators(Actions, dispatch)
    }
}


interface IState {
    searchArr: any,
    checkArr: any,
    selectArr: []
}
interface ISprops{
    [propsName: string]: any
}

const SearchArr = (props: any) => {
    return (
        props.search.map((el,index) => {
            return (
                <Tag
                    key={index}
                    onChange={(selected) => props._this.changeSelect(selected,index)}
                    selected={props._this.state.selectArr[index]}
                >
                    {el.name}
                </Tag>

            )
        })
    )
}


class AllSearch extends React.Component<ISprops, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            searchArr: [],
            checkArr: [],
            selectArr: []
        }
    }
    componentDidMount() {
        document.title = '全部';
    }
    componentWillMount() {
        this.getCourseSeriesData()
    }
    getCourseSeriesData() {
        getCourseSeries().then(res => {
            if(!res.status) {
                this.setState({
                    searchArr: res.data
                })
            }
        })
    }
    clickBtn = () => {
        let { searchArr } = this.state;
        let arr: any = [];
        searchArr.filter((el,index) => {
            if(el.active) {
                arr.push(el.id)
            }
        })
        this.setState({searchArr: arr},() => {
            this.props.history.push({
                pathname: '/courseList',
                query: arr.join(',')
            })
        });
    }
    handleSelect = () => {
        let arr:any = [];
        let { searchArr } = this.state;
        searchArr.map((el,index) => {
            el.active ? arr.push(true) : arr.push(false);
        })
        this.setState({
            selectArr: arr
        })    
    }
    clearBtn = () => {
        let { searchArr, selectArr } = this.state;
        let arr: any = []
        searchArr.map(el => {
            if (el.active) {
                el.active = false;//清除选中数据
            }
            arr.push(false);//清除选中样式
        })
        this.setState({selectArr: arr})

    }
    changeSelect =  (selected,i) => {
        let { searchArr } = this.state;
        let arr: any = [];
        searchArr.filter((el,index) => {
            if(index === i) {
                el.active = selected
            }
        })
        this.handleSelect()
    }
    render() {
        const { searchArr } = this.state;
        return (
            <div className="all-search">
                <div className="clearfix">
                    <SearchArr search={searchArr} ref="search" _this={this} />
                </div>
                <div className="search-foot clearfix">
                    <Button className="btn" type="primary" size="small" onClick={this.clearBtn}>重置</Button>
                    <Button className="btn check" size="small" onClick={this.clickBtn}>确定</Button>
                </div>


            </div>
        );
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(AllSearch);